














































.page-404 {
  width: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
}
.cloud-circle {
  border-radius: 50%;
  filter: url(#filter);
  box-shadow: 400px 400px 60px 0px #fff;
  position: absolute;
}

.island-img {
  height: 72vh;
  width: 80%;
  background: center url("../statics/island.jpeg");
  box-shadow: inset 0px 0px 40px 40px #fff;
  background-size: cover;
}
